import { AppNavigation } from 'nextcloud-vue';
import axios from 'axios';
export default {
  name: 'App',
  components: {
    AppNavigation
  },

  data() {
    return {
      feedLink: ''
    };
  },

  computed: {
    filter() {
      return this.$route.params.filter;
    },

    filters() {
      return this.$store.getters.getFilters;
    },

    menu() {
      return {
        items: this.filters,
        loading: this.loading
      };
    }

  },
  methods: {
    loadFeedLink() {
      axios.get(OC.linkToOCS('apps/activity/api/v2', 2) + 'feed', {
        headers: {
          requesttoken: OC.requestToken
        }
      }).then(response => {
        console.log(response.data.ocs.data);

        if (!_.isUndefined(response.data) && !_.isUndefined(response.data.ocs) && !_.isUndefined(response.data.ocs.data)) {
          this.feedLink = response.data.ocs.data.feedLink;
        } else {
          console.debug("data.ocs.data is undefined or not an array");
        }
      });
    },

    toggleFeedLink() {
      axios.post(OC.linkToOCS('apps/activity/api/v2', 2) + 'feed', {
        enable: !this.feedLink
      }, {
        headers: {
          requesttoken: OC.requestToken
        }
      }).then(response => {
        if (!_.isUndefined(response.data) && !_.isUndefined(response.data.ocs) && !_.isUndefined(response.data.ocs.data)) {
          this.feedLink = response.data.ocs.data.feedLink;
        } else {
          console.debug("data.ocs.data is undefined or not an array");
        }
      });
    },

    onCopy: function () {
      OC.Notification.showTemporary(t('activity', 'Feed link copied!'));
    },

    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    }

  },
  watch: {
    // watch url change and group select
    filter: function (val, old) {
      this.$store.commit('reset');
      this.$store.dispatch('fetchActivities', this.filter); // this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
    }
  },

  beforeMount() {
    this.$store.dispatch('fetchFilters');
    this.$store.dispatch('fetchActivities', this.filter);
    this.loadFeedLink();
  }

};